* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
  outline: none;
}

body {
  background: #f4f5fe;
}

.conteiner {
  width: 100%;
  height: 100%;
  padding: 50px 250px;
}

.conteiner .dashboard {
  display: flex;
  gap: 25px;
}

.conteiner .dashboard h1 {
  color: #000;
  font-family: Gilroy;
  font-size: 48px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.conteiner .dashboard button {
  width: 183px;
  height: 46px;
  flex-shrink: 0;
  border-radius: 10px;
  background: #007fff;
  color: #fff;
  font-family: Gilroy;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  border: none;
  cursor: pointer;
}

.conteiner .table {
  display: flex;
  gap: 25px;
}

.conteiner .table p {
  color: #000;
  font-family: Gilroy;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.conteiner .content {
  width: 100%;
  height: 100%;
}

.conteiner .content .zadachi {
  width: 100%;
  height: 40px;
  display: flex;
  gap: 160px;
  position: relative;
}
.conteiner .content .zadachi p {
  color: #949494;
  font-family: Gilroy;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.conteiner .content .zadachi .box {
  width: 100%;
  height: 1px;
  background: #d5d7e4;
}

.p1 {
  display: flex;
  gap: 100px;
}

.p2 {
  display: flex;
  position: absolute;
  right: 0;
  gap: 80px;
}

.conteiner .content .item {
  width: 100%;
  height: 80px;
  border: 1px solid #000;
  display: flex;
  flex-wrap: wrap;
  gap: 150px;
  position: relative;
  align-items: center;
}

.title {
  width: 400px;
}

.p3 {
  display: flex;
  gap: 40px;
  position: absolute;
  right: 0;
}

.project {
  color: #000;
  font-family: Gilroy;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
.title {
  color: #000;
  font-family: Gilroy;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
.data {
  color: #000;
  font-family: Gilroy;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}
.vremya {
  color: #000;
  font-family: Gilroy;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}



.conteiner .dob {
  width: 500px;
  height: 450px;
  flex-shrink: 0;
  border-radius: 10px;
  background: #fff;
  position: absolute;
  top: 145px;
  left: 450px;
  padding: 30px 60px;
  display: grid;
  display: none;
  animation: fade 1.5s;
}

input {
  width: 100%;
  height: 50px;
  flex-shrink: 0;
  border-radius: 10px;
  border: 1px solid #000;
  background: #fff;
  font-size: 17px;
  padding: 0 13px;
}

select {
  width: 100%;
  height: 50px;
  flex-shrink: 0;
  border-radius: 10px;
  border: 1px solid #000;
  background: #fff;
  font-size: 20px;
}

button {
  width: 100%;
  height: 50px;
  border-radius: 10px;
  background: #0047ff;
  border: none;
  cursor: pointer;
  color: #fff;
  font-family: Gilroy;
  font-size: 15px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

form {
  display: grid;
  gap: 10px;
}

.bx {
  font-size: 32px;
  position: absolute;
  right: 5px;
  top: 5px;
  cursor: pointer;
}

.none {
  display: none;
}